.morphing{position:relative;top:-20%;font-size:20px;color:white;-webkit-filter:contrast(25) blur(1px);filter:contrast(25) blur(1px)}.word{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);animation:word 11s infinite ease-in-out}.word:nth-child(1){animation-delay:-11s}.word:nth-child(2){animation-delay:-9.625s}.word:nth-child(3){animation-delay:-8.25s}.word:nth-child(4){animation-delay:-6.875s}.word:nth-child(5){animation-delay:-5.5s}.word:nth-child(6){animation-delay:-4.125s}.word:nth-child(7){animation-delay:-2.75s}@keyframes word{0%,5%,100%{-webkit-filter:blur(0px);filter:blur(0px);opacity:1}20%,80%{-webkit-filter:blur(1em);filter:blur(1em);opacity:0}}

