.landing-tile {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.top-star-controller {
  transform: rotate(104deg);
  z-index: -1;
  position: absolute;
  top: -5rem;
  transform: rotate(104deg);
  z-index: -1;
  overflow: hidden;
}

.greating-compass-container {
  position: absolute;
  height: 100vh;
  left: 1rem;
  top: 2rem;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}

.compass-icon {
  animation: spin 5s ease-in-out both 1s;
  align-self: center;
}
.landing-greeting {
  opacity: 0;
  animation: show 1s ease-in-out forwards 6s;
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  margin-bottom: -10rem;
  align-self: center;
}
.down-arrow {
  height: 2rem;
}
.arrow {
  opacity: 0;
  animation: show 3s ease-in-out forwards 6s;
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.arrow span {
  display: block;
  width: 30px;
  height: 30px;
  border-bottom: 5px solid #06a8ff;
  border-right: 5px solid #06a8ff;
  transform: rotate(45deg);
  margin: -10px;
  animation: animate 2s infinite;
}
.arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.arrow span:nth-child(3) {
  animation-delay: -0.4s;
}
@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0);
    z-index: 2;
    opacity: 0;
  }
  100% {
    transform: rotate(360deg);
    z-index: 2;
    opacity: 1;
  }
}

@keyframes show {
  0% {
    z-index: 2;
    opacity: 0;
  }
  100% {
    z-index: 2;
    opacity: 1;
  }
}

